import {
  Autocomplete,
  CircularProgress,
  Slider,
  TextField,
  Typography,
} from "@mui/material";
import React, { useState } from "react";
import { useRef } from "react";
import { useEffect } from "react";
import styled from "styled-components";
import Swal from "sweetalert2";
import { bucketUrl } from "../config";

const Component = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  max-width: 1200px;
  @media (min-width: 1600px) {
    max-width: 1200px;
    margin-top: 5vh;
  }
  @media(max-height:640px){
    margin-top: 0;
  }
`;
const LeftComponet = styled.form`
  /* width:100%; */
  flex: 1;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  height: 100%;
`;
const LeftComponentInside = styled.div`
  padding: 1rem 5rem;
  max-width: 500px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
`;
const Title = styled.h2`
  font-weight: 500;
  margin: 10px;
  font-size: 1.9rem;
`;
const Message = styled.h5`
  font-weight: 400;
  font-size: 1.3rem;
  padding: 0;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  align-items: center;
  text-align: center;
  color: #5a5b6a;
  max-width: 26rem;
  /* font-size: 22px; */
  margin: 0;
`;
const Label = styled.div`
  font-weight: 400;
  /* margin-top: 20px; */
  width: 100%;
  display: flex;

  justify-content: space-between;
  /* margin-bottom: 2rem; */
  /* margin-bottom: 2rem; */
`;

const Span = styled.span`
  font-weight: 400;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
`;
const InputComponent = styled.div`
  width: 100%;
  height: 50%;
  font-size: 0.9rem;
  /* margin-bottom: 4rem; */
  background-color: #f9f9fa;
  display: flex;
  flex-direction: column;
  padding: 1.5rem 0;
  align-items: center;
  justify-content: center;
  padding-top: 1rem;
  margin: .5rem 0;
  @media (min-height: 680px) {
    margin: 1.5rem 0;
    /* margin-top: 1rem; */
  }
  @media (min-height: 1000px) {
    padding-top: 3rem;
    padding-bottom: 4rem;
  }
  
`;
const TotalInput = styled.div`
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 3rem;
`;

const Select = styled(Autocomplete)`
  width: 80%;
`;
const Input = styled(TextField)`
  width: 80%;
  margin: 3rem 0 !important;
`;

const SubmitButton = styled.button`
  padding: 8px;
  border-radius: 6px;
  outline: none;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  border: none;

  background-color: ${(props) => (props.isDisabled ? "#E1E1E3" : "#FFD740")};
  color: ${(props) => (props.isDisabled ? "#5A5B6A" : "#000000")};
  display: flex;
  align-items: center;
  justify-content: ${(props) => (props.loading ? "space-between" : "center")};
  width: 100%;
  &:hover {
    background-color: #ffc400;
    background-color: ${(props) => (props.isDisabled ? "#E1E1E3" : "#FFC400")};
  }
  @media (min-height: 745px) {
    margin: 20px 0;
  }
`;
const RightComponent = styled.div`
  flex: 1;
  display: flex;
  width: 100%;
  align-items: center;
`;
const JsonComponent = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  @media (min-height: 700px) {
    margin-top: 1rem;
  }
`;
const Image = styled.img`
  height: ${(props) => `calc(${props.minHeight}px )`};
  margin-top: ${(props) => props.topMargin}px;
  @media (max-height: 830px) {
    height: ${(props) => `calc(${props.minHeight}px - ${props.topMargin}px)`};
  }
  @media (max-height: 650px) {
    /* height: ${(props) => `calc(${props.minHeight}px - ${props.topMargin}px)`}; */
    margin-top: 1rem;
  }
`;

const InsideDiv = styled.div`
  position: relative;
`;
const LeftLines = styled.div`
  /* padding: 1px 0; */
  /* background-color: #000; */
  width: 100%;
  margin-left: 2ch;
  border-bottom: 1px solid #000;
`;
const LeftLineComponent = styled.div`
  position: absolute;
  bottom: ${(props) => (props.screenHeight >= 912 ? "11%" : "8.4%")};
  width: ${(props) => (props.screenHeight >= 912 ? " 78%" : "90%")};
  right: ${(props) => (props.screenHeight >= 912 ? "-48%" : "-58%")};
  @media (max-height: 840px) {
    bottom: 5%;
    width: 100%;
    right: -67%;
  }
  @media (max-height: 790px) {
    bottom: 5%;
    width: 100%;
    right: -67%;
  }
  @media (max-height: 750px) {
    bottom: 4%;
    width: 100%;
    right: -67%;
  }
  @media (max-height: 700px) {
    bottom: 3%;
    width: 100%;
    right: -67%;
  }
  display: flex;
  /* width: 100%; */
  flex-direction: column;
  align-items: flex-end;
`;
const RightLineComponent = styled.div`
  position: absolute;
  top: 5.7%;
  width: 74%;
  right: -38%;
  display: flex;
  /* width: 100%; */
  flex-direction: column;
  align-items: flex-end;
`;
const UpperText = styled.h5`
  font-family: "Quicksand";
  font-style: normal;
  font-weight: 700;
  font-size: 0.8rem;
  color: #474bff;
  text-transform: uppercase;
  margin: 0;
  margin: 0.2rem 0;
  text-align: right;
  max-width: 9rem;
  margin-left: 2ch;
  line-height: 1.2rem;
`;
const LowerText = styled.h6`
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 0.82rem;
  margin: 0;
  color: #5a5b6a;
  max-width: 7rem;
  text-align: right;
  margin: 0 0.2rem;
  margin: 0.2rem 0;
  line-height: 1.2rem;
`;
const AgencySize = ({ activeStep, setActiveStep, formData, setFormData }) => {
  const [data, setData] = useState([
    {
      label: "Delhi",
      value: "Delhi",
    },
  ]);
  const [kmsValue, setKmsValue] = useState(10);
  const [employeesNo, setEmoloyeeNo] = useState(10);
  const [height, setHeight] = useState(0);
  const [screenHeight, setScreenHeight] = useState();
  // const ref = useRef();
  const mobileRef = useRef();
  const [error, setError] = useState(false);
  const [loading, setLoading] = useState(false);
  const [topMargin, setTopMargin] = useState(0);
  const ref = useRef();
  const refMargin = useRef();
  const getHeight = () => {
    console.log("Hi");
    setHeight(ref.current.clientHeight);
    setTopMargin(refMargin.current.clientHeight);
  };
  useEffect(() => {
    getHeight();
    window.addEventListener("resize", getHeight);
    return window.removeEventListener("resize", getHeight);
  }, []);

  useEffect(() => {
    const getHeight = () => {
      setScreenHeight(window.innerHeight);
      setHeight(ref.current.clientHeight);
    };
  }, []);
  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true)
    if (!kmsValue || !employeesNo) {
      setLoading(false)
      Swal.fire({
        icon: "warning",
        title: "Incomplete",
        text: "Please fill all the fields",
      });
      return;
    }
    const data = {
      kmsValue: kmsValue,
      employeesNo: employeesNo,
    };
    setFormData((prev) => ({ ...prev, ...data }));
    var myHeaders = new Headers();
    myHeaders.append("Authorization", "Bearer " + formData.token.access);

    var formdata = new FormData();
    formdata.append("short_name", formData.shortAgencyName);
    formdata.append("full_name", formData.agencyName);
    formdata.append("address", "in" || formData.location);
    formdata.append("pin_code", formData.pinCode);
    formdata.append("company_type", "GN");
    formdata.append("country", formData.country);
    formdata.append("state", formData.state);
    formdata.append("user", formData.token.user_id);
    formdata.append("company_logo", formData.logo);
    formdata.append("jurisdiction", formData.jurisdiction);
    formdata.append("number_of_employees", employeesNo);
    formdata.append("kms_of_road", kmsValue);

    var requestOptions = {
      method: "POST",
      headers: myHeaders,
      body: formdata,
      redirect: "follow",
    };

    fetch("https://sender.paplilabs.com/company/v1/", requestOptions)
      .then((res) => {
        setLoading(false)
        if (res.status === 400) {
          Swal.fire("Already exist", "User already exist", "warning");
        } else {
          Swal.fire(
            "Account Created",
            "Account successfully created",
            "success"
          );
        }
        return res.json();
      })

      .catch((err) => {
        setLoading(false)
        console.log("Hi");
        Swal.fire("Went wrong", "Something went wrong", "error");
      });

  };
  // console.log(screenHeight);
  return (
    <Component>
      <LeftComponet onSubmit={handleSubmit} ref={ref}>
        <LeftComponentInside>
          <Title ref={refMargin}>Your Agency</Title>
          <Message>
            Just an extra bit information so we can understand you better.
          </Message>
          <InputComponent>
            <TotalInput>
              <Label>
                <Span>KMS OF ROAD</Span>
                <Span>{kmsValue} Kms</Span>
              </Label>
              <Slider
                value={kmsValue}
                required
                onChange={(val) => {
                  setKmsValue(val.target.value);
                  setFormData((prev) => ({
                    ...prev,
                    kmsValue: val.target.value,
                  }));
                }}
                min={10}
                max={10000}
              />
              <Label>
                <Span>Kms of road you can cover on monthly basis.</Span>
              </Label>
            </TotalInput>
            <TotalInput>
              <Label>
                <Span>EMPLOYEES</Span>
                <Span>{employeesNo}</Span>
              </Label>
              <Slider
                required
                value={employeesNo}
                onChange={(val) => {
                  setEmoloyeeNo(val.target.value);
                  setFormData((prev) => ({
                    ...prev,
                    employeesNo: val.target.value,
                  }));
                }}
                min={10}
                max={1000}
              />
              <Label>
                <Span>
                  Employees you have in agency that will be looking to use this
                  application.
                </Span>
              </Label>
            </TotalInput>
          </InputComponent>
          <SubmitButton disabled={error} isDisabled={error} loading={loading}>
            {loading ? <div /> : <></>}
            <span>Continue</span>
            {loading ? <CircularProgress size="1rem" /> : <></>}
          </SubmitButton>
        </LeftComponentInside>
      </LeftComponet>
      <RightComponent>
        <JsonComponent>
          <InsideDiv>
            <Image
              minHeight={height}
              topMargin={topMargin * 2}
              src={bucketUrl + "/agencySize.svg"}
            />
            <LeftLineComponent screenHeight={screenHeight}>
              <UpperText>Invite members to join your agency</UpperText>
              <LeftLines />
              <LowerText>
                Share the invite link among your members and get them onboarded.
              </LowerText>
            </LeftLineComponent>
          </InsideDiv>
        </JsonComponent>
      </RightComponent>
    </Component>
  );
};

export default AgencySize;
