import * as React from "react";
import Box from "@mui/material/Box";
import Stepper from "@mui/material/Stepper";
import Step from "@mui/material/Step";
import StepLabel from "@mui/material/StepLabel";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import styled from "styled-components";

const steps = [
  "Phone Number",
  "Email",
  "Name",
  "Location",
  "Agency Address",
  "Agency Name",
  "Agency Size",
];
const Common = styled.span`
  font-size: 0.8rem;
`;
const Label = styled.div`
  font-size: 1.2rem;
`;
const CustomBox = styled(Box)`
  min-width: 50%;
  @media (min-width: 5000px) {
    min-width: 30%;
  }
`;
const Circle = styled.div`
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: ${(props) =>
    props.active||props.fullyCompleted
      ? "black"
      : props.currentActive > props.index
      ? "#7B7B7B"
      : "transparent"};
  border: 1px solid
    ${(props) =>
      props.active
        ? "black"
        : props.currentActive > props.index
        ? "#7B7B7B"
        : "rgba(0,0,0,0.5)"};
  color: ${(props) =>
    props.active
      ? "white"
      : props.currentActive > props.index
      ? "#FFFFFF"
      : "rbga(0,0,0,0.9)"};
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
`;

export default function StepperComponent({
  activeStep,
  setActiveStep,
  completedStep,
  setCompletedStep,
}) {
  return (
    <CustomBox>
      <Stepper activeStep={activeStep}>
        {steps.map((label, index) => {
          return (
            <Step
              style={{ backgroundColor: "black !important" }}
              color="standard"
              key={label}
            >
              {index === activeStep ? (
                <StepLabel icon={<Circle active={1}>{index + 1}</Circle>}>
                  <Common>user</Common>
                  <Label>{label}</Label>
                </StepLabel>
              ) : (
                <StepLabel
                  icon={
                    <Circle
                      active={0}
                      currentActive={completedStep}
                      fullyCompleted={completedStep>3&&index<4}
                      onClick={() => {
                        if(completedStep>3&&index<4){

                        }
                        else if (completedStep > index) {
                          setActiveStep(index);
                        }
                      }}
                      index={index}
                    >
                      {index + 1}
                    </Circle>
                  }
                ></StepLabel>
              )}
            </Step>
          );
        })}
      </Stepper>
    </CustomBox>
  );
}
