import {
  Alert,
  IconButton,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import React, { useState } from "react";
import { useRef } from "react";
import { useEffect } from "react";
import styled from "styled-components";
import { bucketUrl } from "../config";
import "./modification.css";
import Autocomplete from "react-google-autocomplete";
import { Close } from "@mui/icons-material";
import { ErrorEmail } from "../GlobalStyle";

const YOUR_GOOGLE_MAPS_API_KEY = "AIzaSyAl2m9nZGMGgyVWd4LllVgH2oH6Q6KG0uM";
const Component = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  max-width: 1200px;
  @media (min-width: 1600px) {
    max-width: 1200px;
    margin-top: 5vh;
  }
  @media (max-height: 710px) {
    margin-top: 0;
  }
`;
const LeftComponet = styled.form`
  /* width:100%; */
  flex: 1;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  height: 100%;
`;
const LeftComponentInside = styled.div`
  padding: 1rem 5rem;
  max-width: 500px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
`;
const Title = styled.h2`
  font-weight: 500;
  margin: 10px;
  font-size: 1.9rem;
`;
const Message = styled.h5`
  font-weight: 400;
  font-size: 1.3rem;
  padding: 0;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  align-items: center;
  text-align: center;
  color: #5a5b6a;
  max-width: 26rem;
  /* font-size: 22px; */
  margin: 0;
`;
const Label = styled.div`
  font-weight: 400;
  margin-top: 20px;
`;
const Bold = styled.span`
  font-weight: 500;
`;
const InputComponent = styled.div`
  width: 100%;
  height: 50%;
  @media (min-height: 620px) {
    margin: 2.5rem 0;
  }
  /* margin-bottom: 4rem; */
  background-color: #f9f9fa;
  display: flex;
  flex-direction: column;
  padding: 2rem 0;

  align-items: center;
  justify-content: center;
`;
const Select = styled(Autocomplete)`
  width: 80%;
  outline: none !important;
  /* padding: ; */
  height: 3rem;
  /* padding: 0 1rem; */
  border: none;
  background-color: transparent;

  &:focus {
    border-bottom: 2px solid #1976d2;
    /* border-bottom-width: 2px;
    border-bottom-style: solid;
    border-bottom-color: rgb(25, 118, 210); */
  }
  border-bottom: 2px solid #646464;
  &::after {
    border-bottom: 2px solid #1976d2;
    border-bottom-width: 2px;
    border-bottom-style: solid;
    border-bottom-color: rgb(25, 118, 210);
    left: 0;
    bottom: 0;
    content: "";
    position: absolute;
    right: 0;
    -webkit-transform: scaleX(0);
    -moz-transform: scaleX(0);
    -ms-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: -webkit-transform 200ms cubic-bezier(0, 0, 0.2, 1) 0ms;
    transition: transform 200ms cubic-bezier(0, 0, 0.2, 1) 0ms;
    pointer-events: none;
  }
`;
const Input = styled(TextField)`
  width: 80%;
  margin: 3rem 0 !important;
  outline: none !important;
`;
const SubmitButton = styled.button`
  /* margin: 20px 0; */
  padding: 8px;
  border-radius: 6px;
  outline: none;
  border: none;
  background-color: #efd97b;
  width: 100%;
  :hover {
    background-color: rgba(239, 217, 123, 0.9);
  }
`;
const RightComponent = styled.div`
  flex: 1;
  display: flex;
  width: 100%;
  align-items: center;
`;
const JsonComponent = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;

  @media (min-height: 680px) {
    /* margin-top: ${(props) => props.topMargin}px; */
    margin-top: 1rem;
  }
`;
const Image = styled.img`
  /* max-height: 75vh; */

  height: ${(props) => `calc(${props.minHeight}px )`};
  @media (min-height: 650px) {
    margin-top: ${(props) => props.topMargin / 2}px;
  }
  @media (min-height: 670px) {
    margin-top: ${(props) => props.topMargin}px;
  }
`;
const AgencyAddress = ({
  activeStep,
  setActiveStep,
  formData,
  setFormData,
  completedStep,
  setCompletedStep,
}) => {
  const [data, setData] = useState("");
  const [apartment, setApartment] = useState("");
  const [location, setLocation] = useState("");
  const [height, setHeight] = useState(0);
  const [topMargin, setTopMargin] = useState(0);
  const [open, setOpen] = React.useState(false);
  const [locationError, setLocationError] = useState(false);
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };
  const action = (
    <>
      {/* <Button color="secondary" size="small" onClick={handleClose}>
        UNDO
      </Button> */}
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleClose}
      >
        <Close fontSize="small" />
      </IconButton>
    </>
  );
  const ref = useRef();
  const refMargin = useRef();
  const getHeight = () => {
    console.log("Hi");
    setHeight(ref.current.clientHeight);
    setTopMargin(refMargin.current.clientHeight);
  };
  useEffect(() => {
    if (completedStep < 5) {
      handleOpen();
    }
    getHeight();
    setLocation(formData.location);
    setApartment(formData.apartment);
    window.addEventListener("resize", getHeight);
    return window.removeEventListener("resize", getHeight);
  }, []);
  const handleSubmit = (e) => {
    e.preventDefault();
    // const locat = location;
    // const apartment = e.target.apartment.value;
    let locationNew = "";
    if (/^\s*$/.test(location)) {
      setLocationError(true);
      return;
    }
    if (!location) {
      locationNew = e.target.location.value;
    } else {
      locationNew = location;
    }
    const inputData = {
      location: locationNew,
      apartment: apartment,
    };
    console.log(location);
    setFormData((formData) => ({ ...formData, ...inputData }));
    setActiveStep(5);
    if (completedStep < 5) {
      setCompletedStep(5);
    }
  };
  return (
    <Component className="agencyAddress">
      <LeftComponet onSubmit={handleSubmit} ref={ref}>
        <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
          <Alert
            onClose={handleClose}
            severity="success"
            sx={{ width: "100%" }}
          >
            User account created successfully!
          </Alert>
        </Snackbar>
        <LeftComponentInside>
          <Title ref={refMargin}>Your Agency Address</Title>
          <Message>
            We need a physical address to comply with anti-spam laws.
          </Message>
          <InputComponent>
            <Select
              apiKey={YOUR_GOOGLE_MAPS_API_KEY}
              // value={location}
              required
              name="location"
              value={location}
              onChange={(e) => {
                setLocation(e.target.value);
              }}
              onPlaceSelected={(place) => {
                setLocation(place.formatted_address);
                if (!/^\s*$/.test(place.formatted_address)) {
                  setLocationError(false);
                }
              }}
            />
            {locationError && (
              <ErrorEmail icon={<></>}>
                Location Must not be empty string or white space
              </ErrorEmail>
            )}

            <Input
              variant="standard"
              name="apartment"
              label="Apartment/Suite (Optional)"
              value={apartment}
              onChange={(e) => setApartment(e.target.value)}
            />
          </InputComponent>
          <SubmitButton type="submit">Continue</SubmitButton>
        </LeftComponentInside>
      </LeftComponet>
      <RightComponent>
        <JsonComponent>
          <Image
            minHeight={height}
            topMargin={topMargin}
            src={bucketUrl + "/agencyAddress.svg"}
          />
        </JsonComponent>
      </RightComponent>
    </Component>
  );
};

export default AgencyAddress;
