import { Button } from "@mui/material";
import React from "react";
import { Component, Link, Lower, Upper } from "./FooterStyle";

const Footer = ({ activeStep, setActiveStep }) => {
    return (
        <Component >
            {!activeStep ? (
                <Upper>
                    By conitnuing you’re agreeing to out customers terms of
                    service, privacy policy and cookie policy.
                </Upper>
            ) : (
                <></>
            )}

            <Lower footer={activeStep===-1}>
                <Link href="https://novaeavenue.com/contactus">Contact Us</Link>
                <Link href="#">Legal Terms</Link>
            </Lower>
        </Component>
    );
};

export default Footer;
