import React, { useEffect, useState } from "react";
import styled from "styled-components";
import {
  Alert,
  Autocomplete,
  Checkbox,
  CircularProgress,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  FormControl,
  FormControlLabel,
  IconButton,
  InputAdornment,
  InputLabel,
  Radio,
  Snackbar,
  TextField,
} from "@mui/material";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import { CheckBox, Visibility, VisibilityOff } from "@mui/icons-material";
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import { useGeolocated } from "react-geolocated";
import MobileDetect from "mobile-detect";
import Swal from "sweetalert2";
import { ErrorEmail } from "../GlobalStyle";

const FormDiv = styled.form`
  @media (min-height: 640px) {
    margin-top: 6vh;
    margin: 20px 0;
  }
  /* width: 40%; */
  /* margin-top: 6vh; */
  height: 80%;
  display: flex;
  flex-direction: column;
  text-align: center;
  /* align-items: center; */
  /* justify-content: space-between; */
  font-family: "Roboto", sans-serif;
  /* background-color: #000; */
`;
const Upper = styled.div``;
const Title = styled.h2`
  font-weight: 500;
  margin: 10px;
  font-size: 1.5rem;
`;

const InputComponent = styled.div`
  width: 100%;
  height: 100%;
  @media (min-height: 640px) {
    margin: 25px 0;
    /* margin-top: 6vh; */
  }

  background-color: #f9f9fa;
  display: flex;
  flex-direction: column;

  align-items: center;
  justify-content: space-between;
`;

const InputLine = styled.div`
  margin: 30px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 80%;
  height: 100%;
`;

const Input = styled(TextField)`
  width: 100%;
  margin: 1rem 0 !important;
  padding: 3px 2px;
`;

const Button = styled.button`
  margin: 20px 0;
  padding: 8px;
  border-radius: 6px;
  outline: none;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  border: none;
  background-color: ${(props) => (props.isDisabled ? "#E1E1E3" : "#FFD740")};
  color: ${(props) => (props.isDisabled ? "#5A5B6A" : "#000000")};
  display: flex;
  align-items: center;
  justify-content: ${(props) => (props.loading ? "space-between" : "center")};
  &:hover {
    background-color: #ffc400;
    background-color: ${(props) => (props.isDisabled ? "#E1E1E3" : "#FFC400")};
  }
`;

const Location = ({
  activeStep,
  setActiveStep,
  formData,
  setFormData,
  completedStep,
  setCompletedStep,
}) => {
  const [countries, setCountries] = useState([]);
  const [currentCountry, setCurrentCountry] = useState([]);
  const [states, setStates] = useState([]);
  const [currentState, setCurrentState] = useState([]);
  const [pincode, setPincode] = useState("");
  // const [open, setOpen] = useState(false);
  const [error, setError] = useState(false);
  const [loading, setLoading] = useState(false);
  const [open, setOpen] = useState(false);
  const [isAccepted, setIsAccepted] = useState(false);
  const [pinError, setPinError] = useState(false);

  // const theme = useTheme();
  // const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = (ch) => {
    setIsAccepted(ch);
    setOpen(false);
    if (ch == 1) {
      handleSubmit();
    }
  };

  const { coords, isGeolocationAvailable, isGeolocationEnabled } =
    useGeolocated({
      positionOptions: {
        enableHighAccuracy: false,
      },
      userDecisionTimeout: 5000,
    });
  // console.log(coords);
  useEffect(() => {
    if (isGeolocationEnabled && coords) {
      // console.log("hi");
      var requestOptions = {
        method: "GET",
        redirect: "follow",
      };

      fetch(
        `https://sender.paplilabs.com/maps/Igeocoding/${coords.latitude},${coords.longitude}/`,
        requestOptions
      )
        .then((response) => response.json())
        .then((result) => result.result)
        .then((result) => {
          const getCountries = async () => {
            fetch(
              "https://test.paplilabs.com/login_api/country/?limit=230&offset=0"
            )
              .then((res) => res.json())
              .then((res) => {
                // // console.log(res.count);
                const countriesData = [];
                res.results.map((country) => {
                  // console.log(result);
                  if (formData.country) {
                    if (formData.country === country.name) {
                      setCurrentCountry({
                        label: country.name,
                        value: country.code,
                      });
                      getStates(country.code, formData.state);
                    }
                  } else if (country.name === result[0].country) {
                    setCurrentCountry({
                      label: country.name,
                      value: country.code,
                    });
                    getStates(country.code, result[0].state);
                  }
                  countriesData.push({
                    label: country.name,
                    value: country.code,
                  });
                });
                setCountries(countriesData);
              });
          };
          getCountries();
        })
        .catch((error) => console.log("error", error));
    } else if(!isGeolocationEnabled) {
      Swal.fire({
        title: "Oops!",
        text: "Please enable your location",
        icon: "error",
        confirmButtonText: "OK",
      });
    }
  }, [isGeolocationEnabled, isGeolocationAvailable, coords]);

  useEffect(() => {
    // setActiveStep(0);
    let formDatas = JSON.parse(localStorage.getItem("formDatas"));
    setPincode(formData.pinCode || "");
  }, []);
  const getStates = (code, stateName) => {
    fetch(`https://test.paplilabs.com/login_api/state/${code}/`)
      .then((res) => res.json())
      .then((res) => {
        // console.log(res);
        const stateDatas = [];

        res.map((state) => {
          if (stateName === state.region) {
            setCurrentState({
              label: state.region,
              value: state.region,
            });
          }
          stateDatas.push({
            label: state.region,
            value: state.region,
          });
        });
        setStates(stateDatas);
        // // console.log(stateDatas);
      });
  };
  const handleSubmit = (e) => {
    if (e) {
      e.preventDefault();
    }
    setLoading(true);
    if (!/^(\d{4}|\d{6})$/.test(pincode)) {
      setPinError(true);
      setLoading(false);
      return;
    }
    if (!isGeolocationAvailable || !isGeolocationEnabled) {
      Swal.fire({
        title: "Oops...",
        text: "Please enable your location",
        icon: "error",
        confirmButtonText: "OK",
      });
      setLoading(false);
      return;
    }
    const userData = JSON.parse(localStorage.getItem("formDatas"));

    const data = {
      country: currentCountry.label,
      state: currentState.label,
      pinCode: pincode,
      cordinate_X: coords.latitude,
      cordinate_Y: coords.longitude,
    };
    const dataToSent = {
      email: formData.email,
      password: formData.pass,
      profile: {
        first_name: formData.firstName,
        last_name: formData.lastName,
        phone_number: formData.number,
        user_type: "I",
        country: currentCountry.label,
        state: currentState.label,
        pin_code: pincode,
        cordinate_X: coords.latitude,
        cordinate_Y: coords.longitude,
      },
      device: {
        platform: "ANR",
        dev_id:
          "fOJF2n04Q62MHy2i9rAGLs:APA91bHBUYJVPMk8_eKOSm15xcN6Istx4WenBTB1g_fFZ4qnC50VSTds4-a0R3ThGnqBlqTtDcsqdXCydxLGT-PJowMZ8Me3O1-NtzCNKiYmwJrEYliWsPv_RpG-ExcZpycKW9xUlXAB",
      },
    };
    setFormData((formData) => ({ ...formData, ...data }));
    fetch("https://sender.paplilabs.com/login_api/signup/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(dataToSent),
    })
      .then((res) => {
        if (res.status === 400) {
          setLoading(false);
          res.json().then((newRes) => {
            if (newRes.profile) {
              if (newRes.profile.email) {
                Swal.fire("Invalid", "Invalid Email", "warning");
              } else if (newRes.profile.phone_number) {
                Swal.fire("Invalid", "Invalid Phone Number", "warning");
              }
            }
          });
          return;
        } else {
          // Swal.fire(
          //   "Account Created",
          //   "Account successfully created",
          //   "success"
          // );
          fetch("https://sender.paplilabs.com/login_api/signin/", {
            method: "post",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({
              email: formData.email,
              password: formData.pass,
            }),
          })
            .then((res) => {
              // setFormData((prev)=>({...prev,token:}))
              return res.json();
            })
            .then((res) => {
              if (res.token) {
                setLoading(false);
                const newData = {
                  token: res.token,
                  user: res.user,
                  country: currentCountry.value,
                  state: currentState.label,
                };
                setFormData((prev) => ({ ...prev, ...newData }));
                setActiveStep(4);
                if (completedStep < 4) {
                  setCompletedStep(4);
                }
              }
            });
        }
        return res.json();
      })
      .catch((err) => {
        setLoading(false);
        // console.log("Hi");
        Swal.fire("Went wrong", "Something went wrong", "error");
      });

    // // console.log(data);
  };
  return (
    <FormDiv onSubmit={handleSubmit}>
      <Upper>
        <Title>
          Allow us, to get your location so <br /> we can set up map for you.
        </Title>
      </Upper>
      <InputComponent>
        <InputLine>
          <Autocomplete
            id="disable-close-on-select"
            options={countries}
            style={{ width: "100%" }}
            value={currentCountry}
            onChange={(e, val) => {
              setCurrentCountry(val);
              getStates(val.value);
            }}
            v
            // inputValue={currentCountry}
            renderInput={(params) => (
              <Input
                {...params}
                label="Country"
                variant="standard"
                name="country"
                required
                // onChange={(e) => // console.log(e.target.value)}
              />
            )}
          />
          <Autocomplete
            id="disable-close-on-select"
            options={states}
            style={{ width: "100%" }}
            value={currentState}
            onChange={(e, val) => {
              setCurrentState(val);
              // console.log(val);
            }}
            renderInput={(params) => (
              <Input
                {...params}
                label="State"
                variant="standard"
                name="state"
                required
              />
            )}
          />
          <Input
            variant="standard"
            value={pincode}
            onChange={(e) => {
              setPincode(e.target.value);
              if (/^(\d{4}|\d{6})$/.test(e.target.value)) {
                setPinError(false);
              }
            }}
            name="pinCode"
            required
            type="number"
            label="Pin Code"
          />
          {pinError && (
            <ErrorEmail icon={<></>}>Please enter valid pin code</ErrorEmail>
          )}
        </InputLine>
      </InputComponent>
      <Dialog
        // fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
        className="dialog"
      >
        <DialogTitle id="responsive-dialog-title">
          {"Are you sure?"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            You will not be able to change your previous data later.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button type="button" autoFocus onClick={() => handleClose(0)}>
            Disagree
          </Button>
          <Button type="button" onClick={() => handleClose(1)} autoFocus>
            Agree
          </Button>
        </DialogActions>
      </Dialog>
      <Button
        type="button"
        onClick={handleClickOpen}
        disabled={error}
        isDisabled={error}
        loading={loading}
      >
        {loading ? <div /> : <></>}
        <span>Continue</span>
        {loading ? <CircularProgress size="1rem" /> : <></>}
      </Button>
    </FormDiv>
  );
};

export default Location;
