import { CheckCircle, CheckCircleOutline } from "@mui/icons-material";
import {
  Autocomplete,
  FormControl,
  IconButton,
  Input,
  InputAdornment,
  InputLabel,
  TextField,
  Typography,
} from "@mui/material";
import React, { useState } from "react";
import { useRef } from "react";
import { useEffect } from "react";
import styled from "styled-components";
import { bucketUrl } from "../config";
import { ErrorEmail } from "../GlobalStyle";

const Component = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  max-width: 1200px;
  @media (min-height: 910px) {
    margin-top: 5vh;
  }
  @media (min-width: 1600px) {
    max-width: 1200px;
  }
`;
const LeftComponet = styled.form`
  /* width:100%; */
  flex: 1;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
`;
const LeftComponentInside = styled.div`
  /* padding: 1rem 5rem; */
  max-width: 500px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
`;

const Title = styled.h2`
  font-weight: 500;
  font-size: 1.9rem;
  text-align: center;
  max-width: 28rem;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.87);
  margin: 10px;
  @media (max-height: 676px) {
    margin: 0px;
    /* margin-top: 5vh; */
  }
`;
const Message = styled.h5`
  font-weight: 400;
  font-size: 1.3rem;
  padding: 0;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  align-items: center;
  text-align: center;
  color: #5a5b6a;
  max-width: 26rem;
  /* font-size: 22px; */
  margin: 0;
`;
const Label = styled.span`
  font-weight: 400;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.6) !important;
  font-size: 0.8rem;
  margin-top: 0.3rem;
  padding: 0.4rem 2px;

  /* margin-top: 20px; */
`;
const Bold = styled.span`
  font-weight: 500;
`;
const InputComponent = styled.div`
  width: 100%;
  height: 50%;
  background-color: #f9f9fa;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @media (min-height: 840px) {
    margin-bottom: 4rem;
  }
  @media (min-height: 695px) {
    /* margin-bottom: 4rem; */
    margin: 0.5rem 0;
  }
  @media (min-height: 670px) {
    /* margin-bottom: 4rem; */
    /* margin: 0.5rem 0; */
    padding: 1rem 0;
  }
`;
const TotalInput = styled.div`
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 0;
  margin-bottom: 0.3rem;
  @media (min-height: 840px) {
    margin-bottom: 1.5rem;
  }
`;
const TotalInputImage = styled(TotalInput)`
  margin-top: 0;

  margin-bottom: 0.3rem;
  @media (min-height: 840px) {
    margin-top: 1.5rem;
  }
`;
const InputWithIcon = styled(Input)`
  /* width: 80%; */
  /* margin: 15px 0 !important; */
  margin-top: 1rem;
  padding: 0.4rem 2px;
  /* mar */
`;
const EmailVarified = styled(CheckCircle)`
  transform: scale(0.7);
`;
const EmailUnVarified = styled(CheckCircleOutline)`
  transform: scale(0.7);
`;
const Select = styled(Autocomplete)`
  width: 80%;
`;
const InputField = styled(TextField)`
  width: 80%;
  margin: 3rem 0 !important;
`;
const SubmitButton = styled.button`
  padding: 8px;
  border-radius: 6px;
  outline: none;
  border: none;
  width: 80%;
  background-color: #efd97b;
  :hover {
    background-color: rgba(239, 217, 123, 0.9);
  }
  @media (min-height: 710px) {
    margin: 0.5rem 0;
  }
`;
const RightComponent = styled.div`
  flex: 1;
  display: flex;
  width: 50%;
  align-items: center;
`;
const JsonComponent = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 1rem;
`;
const Image = styled.img`
  /* max-height: 75vh; */
  /* min-height: ${(props) => props.minHeight}px; */
  /* min-height: ${(props) => `calc(${props.minHeight}px + 0vh)`}; */
  height: ${(props) => `calc(${props.minHeight}px )`};
  margin-top: ${(props) => props.topMargin}px;
  @media (max-height: 860px) {
    /* margin-top: 100vh; */
    height: ${(props) => `calc(${props.minHeight}px - ${props.topMargin}px)`};
  }
`;
const InsideDiv = styled.div`
  position: relative;
`;
const LeftLines = styled.div`
  /* padding: 1px 0; */
  /* background-color: #000; */
  width: 100%;
  margin-left: 2ch;
  border-bottom: 1px solid #000;
`;
const LeftLineComponent = styled.div`
  position: absolute;
  top: 10.7%;
  width: 80%;
  left: -50%;
  display: flex;
  /* width: 100%; */
  flex-direction: column;
  align-items: flex-start;
  @media(max-height:670px){
    width: 88%;
    left: -57%;
    
  }
`;
const RightLineComponent = styled.div`
  position: absolute;
  top: 10.7%;
  width: 80%;
  right: -48%;
  display: flex;
  /* width: 100%; */
  flex-direction: column;
  align-items: flex-end;
  @media(max-height:670px){
    width: 88%;
    right: -57%;
    
  }
`;
const UpperText = styled.h5`
  font-family: "Quicksand";
  font-style: normal;
  font-weight: 700;
  font-size: 0.8rem;
  color: #474bff;
  text-transform: uppercase;
  margin: 0;
  margin: 0.2rem 0;
  text-align: left;
  margin-left: 2ch;
`;
const LowerText = styled.h6`
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 0.82rem;
  margin: 0;
  color: #5a5b6a;
  max-width: 7rem;
  text-align: right;
  margin: 0 0.2rem;
  margin: 0.2rem 0;
`;
const RightLines = styled(LeftLines)`
  margin-left: 0;
  width: calc(100% + 2ch);
`;
const RightUpperText = styled(UpperText)``;
const RightLowerText = styled(LowerText)`
  text-align: left;
`;
const AgencyName = ({
  activeStep,
  setActiveStep,
  formData,
  setFormData,
  completedStep,
  setCompletedStep,
}) => {
  const [isValidName, setisValidName] = useState({
    value: "",
    isValid: false,
  });
  const [isValidShortName, setisValidShortName] = useState({
    value: "",
    isValid: false,
  });
  const [ju, setJu] = useState("");
  const [file, setFile] = useState();
  const [height, setHeight] = useState(0);
  const [topMargin, setTopMargin] = useState(0);
  const [error, setError] = useState(false);
  const [shortError, setShortError] = useState(false);
  const [isValidJurisdiction, setIsValidJurisdiction] = useState(true);
  const ref = useRef();
  const refMargin = useRef();
  const getHeight = () => {
    console.log("hi");
    setHeight(ref.current.clientHeight);
    setTopMargin(refMargin.current.clientHeight);
  };
  useEffect(() => {
    getHeight();
    window.addEventListener("resize", getHeight);

    return window.removeEventListener("resize", getHeight);
  }, []);
  useEffect(() => {
    setisValidName((prev) => ({ ...prev, value: formData.agencyName || "" }));
    setisValidShortName((prev) => ({
      ...prev,
      value: formData.shortAgencyName || "",
    }));
    if (formData.agencyName && formData.shortAgencyName) {
      validateName(formData.agencyName, 1);
      validateShortName(formData.shortAgencyName, 1);
    }
    setJu(formData.jurisdiction);
    setFile([formData.logo]);
  }, []);

  const validateName = (e, ch) => {
    if (ch !== 1) {
      setisValidName((prev) => ({ ...prev, value: e.target.value }));
      if (/^\s*$/.test(e.target.value)){
        setisValidName((prev) => ({ ...prev, isValid: false }));
        return;
      }
    } else {
      setisValidName((prev) => ({ ...prev, value: e }));
      if (/^\s*$/.test(e)){
        setisValidName((prev) => ({ ...prev, isValid: false }));
        return;
      }
    }
    fetch("https://sender.paplilabs.com/company/CmpVal/fullNameVal/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + formData.token.access,
      },
      body: JSON.stringify({
        full_name: ch !== 1 ? e.target.value : e,
        short_name: "",
      }),
    })
      .then((res) => res.json())
      .then((res) => {
        // console.log(res.full_name_exists);
        if (!res.full_name_exists) {
          setisValidName((prev) => ({ ...prev, isValid: true }));
          setError(false);
        } else {
          setisValidName((prev) => ({ ...prev, isValid: false }));
        }
      });
  };
  const validateShortName = (e, ch) => {
    if (ch !== 1) {
      setisValidShortName((prev) => ({ ...prev, value: e.target.value }));
      if (e.target.value.length > 3) {
        setisValidShortName((prev) => ({ ...prev, isValid: false }));
        return;
      }
    } else {
      setisValidShortName((prev) => ({ ...prev, value: e }));
    }
    fetch("https://sender.paplilabs.com/company/CmpVal/shortNameVal/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + formData.token.access,
      },
      body: JSON.stringify({
        short_name: ch !== 1 ? e.target.value : e,
      }),
    })
      .then((res) => res.json())
      .then((res) => {
        if (!res.short_name_exists) {
          setisValidShortName((prev) => ({ ...prev, isValid: true }));
          setShortError(false);
        } else {
          setisValidShortName((prev) => ({ ...prev, isValid: false }));
        }
        // setisValidShortName((prev) => ({ ...prev, isValid: true }));
      });
  };
  const handleSubmit = (e) => {
    e.preventDefault();

    if (!isValidName.isValid && !isValidShortName.isValid) {
      setError(true);
      setShortError(true);
      return;
    }
    if (!isValidName.isValid) {
      setError(true);
      return;
    }
    if (!isValidShortName.isValid) {
      setShortError(true);
      return;
    }
    if (/^\s*$/.test(ju)) {
      setIsValidJurisdiction(false);
      return;
    }
    const data = {
      agencyName: isValidName.value,
      shortAgencyName: isValidShortName.value,
      jurisdiction: e.target.jurisdiction.value,
      logo: e.target.file.files[0],
    };
    setFormData((prev) => ({ ...prev, ...data }));
    setActiveStep(6);
    if (completedStep < 6) {
      setCompletedStep(6);
    }
  };
  return (
    <Component>
      <LeftComponet onSubmit={handleSubmit} ref={ref}>
        <LeftComponentInside>
          <Title ref={refMargin}>Create your Novae Avenue Agency Account</Title>

          <InputComponent>
            <TotalInput>
              <FormControl sx={{ width: "100%" }} variant="standard">
                <InputLabel htmlFor="standard-adornment-password">
                  FULL NAME
                </InputLabel>
                <InputWithIcon
                  id="standard-adornment-password"
                  onChange={(e) => validateName(e)}
                  name="fullName"
                  required
                  value={isValidName.value}
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton aria-label="toggle password visibility">
                        {isValidName.isValid ? (
                          <EmailVarified color="primary" />
                        ) : (
                          <EmailUnVarified />
                        )}
                      </IconButton>
                    </InputAdornment>
                  }
                />
              </FormControl>
              {error ? (
                <ErrorEmail icon={<></>}>
                  Agency with this name already exist.
                </ErrorEmail>
              ) : (
                <Label>Enter the Full Name of your Agency</Label>
              )}
            </TotalInput>
            <TotalInput>
              <FormControl sx={{ width: "100%" }} variant="standard">
                <InputLabel htmlFor="standard-adornment-password">
                  SHORT NAME
                </InputLabel>
                <InputWithIcon
                  required
                  id="standard-adornment-password"
                  onChange={(e) => validateShortName(e)}
                  name="shortName"
                  value={isValidShortName.value}
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton aria-label="toggle password visibility">
                        {isValidShortName.isValid ? (
                          <EmailVarified color="primary" />
                        ) : (
                          <EmailUnVarified />
                        )}
                      </IconButton>
                    </InputAdornment>
                  }
                />
              </FormControl>
              {shortError ? (
                <ErrorEmail icon={<></>}>
                  Agency with this short name already exist.
                </ErrorEmail>
              ) : (
                <Label>Like Public Works Department is known as PWD</Label>
              )}
            </TotalInput>
            <TextField
              sx={{
                width: "70%",
                padding: "0.4rem 2px",
              }}
              label="Jurisdiction"
              name="jurisdiction"
              required
              value={ju}
              onChange={(e) => {
                setJu(e.target.value);
                if (!/^\s*$/.test(e.target.value)) {
                  setIsValidJurisdiction(true);
                }
              }}
              multiline="true"
              minRows={3}
            />
            {!isValidJurisdiction && (
              <ErrorEmail icon={<></>}>
                Jurisdiction cannot be empty string or white space
              </ErrorEmail>
            )}
            <TotalInputImage>
              <FormControl sx={{ width: "100%" }} variant="standard">
                <InputLabel htmlFor="standard-adornment-password">
                  UPLOAD LOGO OF YOUR AGENCY
                </InputLabel>
                <InputWithIcon
                  id="standard-adornment-password"
                  // onChange={(e) => validateShortName(e)}
                  name="file"
                  type="file"
                  required
                  accept="image/*"
                  // value={[formData.logo]}
                  startAdornment={
                    <InputAdornment position="end">
                      <IconButton aria-label="toggle password visibility">
                        <img src={bucketUrl + "/camera.svg"} color="primary" />
                      </IconButton>
                    </InputAdornment>
                  }
                />
              </FormControl>
              <Label>This will help your members to locate faster</Label>
            </TotalInputImage>
          </InputComponent>
          <SubmitButton>Continue</SubmitButton>
        </LeftComponentInside>
      </LeftComponet>
      <RightComponent>
        <JsonComponent>
          <InsideDiv>
            <Image
              minHeight={height}
              topMargin={topMargin / 2}
              src={bucketUrl + "/agencyName.svg"}
            />
            <LeftLineComponent>
              <UpperText>Original</UpperText>
              <LeftLines />
              <LowerText>
                These are all the potholes is present in the country.
              </LowerText>
            </LeftLineComponent>
            <RightLineComponent>
              <RightUpperText>MY List</RightUpperText>
              <RightLines />
              <RightLowerText>
                All the photos taken by your team will be shown here.
              </RightLowerText>
            </RightLineComponent>
          </InsideDiv>
        </JsonComponent>
      </RightComponent>
    </Component>
  );
};

export default AgencyName;
