import React from "react";
import styled from "styled-components";

const Component = styled.div`
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.2rem;
`;
const ComponentInside = styled.div`
  width:100%;
  max-width: calc(1700px + 3vw);
  display: flex;
  justify-content: space-between;
`;
const LeftComponet = styled.div`
  /* width: 100%; */
  /* flex: .7; */
  display: flex;
  justify-content: flex-start;
  max-width: calc(720px + 5vw);
  width: 40%;
`;
const LeftText = styled.a`
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 0.65rem;
  margin: 0 0.5rem;
  color: #5a5b6a;
  text-decoration: none;
`;
const RightText = styled.a`
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 0.8rem;
  font-size: 0.65rem;
  margin: 0 0.5rem;
  color: #787885;
  text-decoration: none;
`;
const RightComponent = styled.div`
  /* flex:1; */
  width: 60%;
  display: flex;
  justify-content: flex-end;
  max-width: 720px;
`;
const SignInFooter = () => {
  return (
    <Component>
      <ComponentInside>
        <LeftComponet>
          <LeftText href="#">©2020 Papli Labs</LeftText>
        </LeftComponet>
        <RightComponent>
          <RightText href="#">Contact Us</RightText>
          <RightText href="#">Legal Terms</RightText>
        </RightComponent>
      </ComponentInside>
    </Component>
  );
};

export default SignInFooter;
