import React, { useEffect, useState } from "react";
import styled from "styled-components";
import {
  Button,
  Checkbox,
  CircularProgress,
  FormControl,
  FormControlLabel,
  IconButton,
  Input,
  InputAdornment,
  InputLabel,
  Radio,
  TextField,
} from "@mui/material";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import { CheckBox, Visibility, VisibilityOff } from "@mui/icons-material";
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import Swal from "sweetalert2";
import { ErrorEmail } from "../GlobalStyle";

const FormDiv = styled.form`
  /* width: 40%; */
  height: 80%;
  display: flex;
  flex-direction: column;
  text-align: center;
  /* align-items: center; */
  /* justify-content: space-between; */
  font-family: "Roboto", sans-serif;
  /* background-color: #000; */
  @media (min-height: 690px) {
    margin: 20px 0;
    margin-top: 6vh;
  }
  /* margin-top: 6vh; */
`;
const Upper = styled.div``;
const Title = styled.h2`
  font-weight: 500;
  margin: 10px;
  font-size: 1.9rem;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
`;
const Message = styled.h5`
  font-weight: 400;
  font-size: 1.3rem;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  padding: 0;
  margin: 0;
`;
const InputComponent = styled.div`
  width: 100%;
  height: 60%;
  @media (min-height: 640px) {
    /* margin-top: 6vh; */
    margin: 25px 0;
  }

  /* background-color: #f9f9fa; */
  display: flex;
  flex-direction: column;

  align-items: center;
  justify-content: center;
`;
const Label = styled.div`
  font-weight: 400;
  margin-top: 20px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
`;
const Bold = styled.span`
  font-weight: 500;
`;
const InputLine = styled.div`
  @media (min-height: 590px) {
    /* margin-top: 6vh; */
    margin: 20px 0;
  }
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 80%;
`;

const InputWithIcon = styled(Input)`
  width: 100%;
  padding: 3px 2px;
`;

const SubmitButton = styled.button`
  margin: 20px 0;
  padding: 8px;
  border-radius: 6px;
  outline: none;
  border: none;
  background-color: #efd97b;
  :hover {
    background-color: rgba(239, 217, 123, 0.9);
  }
  margin: 0px 0;
  padding: 8px;
  border-radius: 6px;
  outline: none;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  border: none;
  background-color: ${(props) => (props.isDisabled ? "#E1E1E3" : "#FFD740")};
  color: ${(props) => (props.isDisabled ? "#5A5B6A" : "#000000")};
  display: flex;
  align-items: center;
  justify-content: ${(props) => (props.loading ? "space-between" : "center")};
  &:hover {
    background-color: #ffc400;
    background-color: ${(props) => (props.isDisabled ? "#E1E1E3" : "#FFC400")};
  }
`;
const EmailVarified = styled(CheckCircleIcon)`
  transform: scale(0.7);
`;
const EmailUnVarified = styled(CheckCircleOutlineIcon)`
  transform: scale(0.7);
`;
const HidePassword = styled(VisibilityOff)`
  transform: scale(0.7);
`;
const ShowPassword = styled(Visibility)`
  transform: scale(0.7);
`;
const IAgree = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`;
const IAgreeMessage = styled.span`
  font-size: 0.7rem;
  font-weight: 400;
`;
const IsOkayContainer = styled.div``;
const IsOkayButton = styled(Button)`
  padding: 5px 2px !important;
  border-radius: 20px !important;
  margin: 2px 2px !important;
  font-size: 0.7rem !important;
  min-width: 100px !important;
  text-transform: lowercase !important  ;
`;
const EmailDiv = styled.div`
  width: 80%;
  margin: 15px 0 !important;
`;

const EmailAndPass = ({
  activeStep,
  setActiveStep,
  formData,
  setFormData,
  completedStep,
  setCompletedStep,
}) => {
  const [values, setValues] = useState({
    password: "",

    showPassword: false,
  });

  const [isValidEmail, setIsValidEmail] = useState(false);

  const [isValidPassword, setIsValidPassword] = useState({
    lowerCase: null,
    upperCase: null,
    number: null,
    specialChar: null,
  });
  const [emailValue, setEmailValue] = useState("");
  const [passValue, setPassValue] = useState("");
  const [emailError, setEmailError] = useState(false);
  const [passwordError, setPasswordError] = useState(false);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    const datas = JSON.parse(localStorage.getItem("formDatas") || {});
    setEmailValue(datas.email || "");
    validateEmail(datas.email || "", "d");
    setPassValue(datas.pass || "");
    validatePassword(datas.pass || "", "d");
  }, []);

  const handleChange = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({
      ...values,
      showPassword: !values.showPassword,
    });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };
  const validateEmail = (e, ch) => {
    // //console.log(e.target.value);
    let val = e;
    if (ch !== "d") {
      val = e.target.value;
    }
    setEmailValue(val);
    if (
      String(val)
        .toLowerCase()
        .match(
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        )
    ) {
      setIsValidEmail(true);
      setEmailError(false);
    } else {
      setIsValidEmail(false);
    }
  };
  const validatePassword = (e, ch) => {
    // console
    let val = e;
    if (ch !== "d") {
      val = e.target.value;
    }
    setPassValue(val);
    //console.log(val);
    if (String(val).match(`(?=.*[A-Z])`)) {
      //console.log("Hi");
      setIsValidPassword((isValidPassword) => ({
        ...isValidPassword,
        upperCase: true,
        isValid: true,
      }));
    } else {
      setIsValidPassword((isValidPassword) => ({
        ...isValidPassword,
        ["upperCase"]: false,
        isValid: false,
      }));
    }
    if (String(val).match(`(?=.*[a-z])`)) {
      //console.log("Hi");
      setIsValidPassword((isValidPassword) => ({
        ...isValidPassword,
        lowerCase: true,
        isValid: true,
      }));
    } else {
      setIsValidPassword((isValidPassword) => ({
        ...isValidPassword,
        lowerCase: false,
        isValid: false,
      }));
    }
    if (val.match(`(?=.*?[0-9])`)) {
      //console.log("Hi");
      setIsValidPassword((isValidPassword) => ({
        ...isValidPassword,
        number: true,
        isValid: true,
      }));
    } else {
      setIsValidPassword((isValidPassword) => ({
        ...isValidPassword,
        number: false,
        isValid: false,
      }));
    }
    if (String(val).match(`(?=.*?[#?!@$%^&*-])`)) {
    
      setIsValidPassword((isValidPassword) => ({
        ...isValidPassword,
        specialChar: true,
        isValid: true,
      }));
    } else {
      setIsValidPassword((isValidPassword) => ({
        ...isValidPassword,
        specialChar: false,
        isValid: false,
      }));
    }
    if (isValidPassword.isValid) {
      setPasswordError(false);
    }
  };

  const handleSubmit = async (e) => {
   
    e.preventDefault();
    setLoading(true);
    const email = e.target.email.value;
    const pass = e.target.pass.value;
    if (!isValidEmail) {
      // Swal.fire("Invalid", "Invalid email", "warning");
      setEmailError(1);
      setLoading(false);
      return;
    }
    if (!isValidPassword.isValid) {
      // Swal.fire("Invalid", "Invalid password", "warning");
      setPasswordError(true);
      setLoading(false);
      return;
    }
    let formDatas = JSON.parse(localStorage.getItem("formDatas"));
    formDatas.email = email;
    formDatas.pass = pass;

    localStorage.setItem("formDatas", JSON.stringify(formDatas));
    const datas = {
      email: email,
      pass: pass,
    };
    setFormData((formData) => ({ ...formData, ...datas }));
    fetch("https://test.paplilabs.com/user/validateEmail/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ email: email }),
    })
      .then((res) => res.json())
      .then((res) => {
        if (res.user_exist === "True") {
          setEmailError(2);
          setLoading(false);
        } else {
          setActiveStep(2);
          if (completedStep < 2) {
            setCompletedStep(2);
          }
        }
      });
    // navigate('/email')
  };
  return (
    <FormDiv onSubmit={handleSubmit}>
      <Upper>
        <Title>Welcome!</Title>
        <Message>Let’s get you started with a free Account.</Message>
      </Upper>
      <InputComponent>
        <Label>
          We suggest using the <Bold> email address that you use at work</Bold>
        </Label>
        <InputLine>
          {/* <FlagComponent>
                        <Flag src='/india.png' />
                        <DropDown />
                    </FlagComponent> */}
          <EmailDiv className="emailDiv">
            <FormControl sx={{ width: "100%" }} variant="standard">
              <InputLabel htmlFor="standard-adornment-password">
                Email
              </InputLabel>
              <InputWithIcon
                id="standard-adornment-password"
                onChange={(e) => validateEmail(e)}
                name="email"
                value={emailValue}
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton aria-label="toggle password visibility">
                      {isValidEmail ? (
                        <EmailVarified color="primary" />
                      ) : (
                        <EmailUnVarified
                          color={emailError ? "error" : "inherit"}
                        />
                      )}
                    </IconButton>
                  </InputAdornment>
                }
              />
            </FormControl>
            {emailError && (
              <ErrorEmail icon={<></>}>
                {emailError === 1 ? "Invalid Email" : "Email Already Exist"}
              </ErrorEmail>
            )}
          </EmailDiv>
          <EmailDiv>
            <FormControl sx={{ width: "100%" }} variant="standard">
              <InputLabel htmlFor="standard-adornment-password">
                Password
              </InputLabel>
              <InputWithIcon
                id="standard-adornment-password"
                type={values.showPassword ? "text" : "password"}
                value={passValue}
                onChange={validatePassword}
                name="pass"
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                      onMouseDown={handleMouseDownPassword}
                    >
                      {values.showPassword ? (
                        <HidePassword
                          color={passwordError ? "error" : "inherit"}
                        />
                      ) : (
                        <ShowPassword
                          color={passwordError ? "error" : "inherit"}
                        />
                      )}
                    </IconButton>
                  </InputAdornment>
                }
              />
            </FormControl>
            {passwordError && (
              <ErrorEmail icon={<></>}>
                Invalid password (Password must contain alteast 1 upper case,
                lower case, number, special character)
              </ErrorEmail>
            )}
          </EmailDiv>
          <IsOkayContainer>
            <IsOkayButton
              color={isValidPassword.upperCase === false ? "error" : "primary"}
              variant="outlined"
            >
              capital letter
            </IsOkayButton>
            <IsOkayButton
              variant="outlined"
              color={
                isValidPassword.specialChar === false ? "error" : "primary"
              }
            >
              spacial character
            </IsOkayButton>
            <IsOkayButton
              variant="outlined"
              color={isValidPassword.lowerCase === false ? "error" : "primary"}
            >
              lowercase letter
            </IsOkayButton>
            <IsOkayButton
              variant="outlined"
              color={isValidPassword.number === false ? "error" : "primary"}
            >
              numbers
            </IsOkayButton>
          </IsOkayContainer>
        </InputLine>
        <IAgree>
          <FormControlLabel control={<Checkbox defaultChecked />} label="" />
          <IAgreeMessage>
            it’s okay to send me emails about Novae Avenue
          </IAgreeMessage>
        </IAgree>
      </InputComponent>{" "}
      <SubmitButton
        disabled={emailError || passwordError}
        isDisabled={emailError || passwordError}
        loading={loading}
      >
        {loading ? <div /> : <></>}
        <span>Continue</span>
        {loading ? <CircularProgress size="1rem" /> : <></>}
      </SubmitButton>
    </FormDiv>
  );
};

export default EmailAndPass;
