import { ArrowDropDown } from "@mui/icons-material";

import React, { useEffect } from "react";
import { Route, Routes } from "react-router-dom";
import styled from "styled-components";
import AgencyAddress from "./AgencyAddress";
import AgencyName from "./AgencyName";
import AgencySize from "./AgencySize";
import EmailAndPass from "./EmailAndPass";
import Location from "./Location";
import Name from "./Name";
import NumberComponent from "./Number";

const Component = styled.div`
  display: flex;
  /* flex-direction: row; */
  /* align-items: center; */
  justify-content: center;
  width: 100%;
  height: 100%;
  @media (min-width: 5000px) {
    /* justify-content: center; */
    /* margin: auto 0; */
  }
  /* min-height: 600px; */
`;
const FormComponent = ({
  activeStep,
  setActiveStep,
  formData,
  setFormData,
  completedStep,
  setCompletedStep
}) => {
  useEffect(() => {
    if (activeStep === -1) {
      setActiveStep(0);
    }
  }, []);

  const getStepContent = (step) => {
    switch (step) {
      case 0:
        return (
          <NumberComponent
            activeStep={activeStep}
            setActiveStep={setActiveStep}
            formData={formData}
            setFormData={setFormData}
            completedStep={completedStep}
            setCompletedStep={setCompletedStep}
          />
        );
      case 1:
        return (
          <EmailAndPass
            activeStep={activeStep}
            setActiveStep={setActiveStep}
            formData={formData}
            setFormData={setFormData}
            completedStep={completedStep}
            setCompletedStep={setCompletedStep}
          />
        );
      case 2:
        return (
          <Name
            activeStep={activeStep}
            setActiveStep={setActiveStep}
            formData={formData}
            setFormData={setFormData}
            completedStep={completedStep}
            setCompletedStep={setCompletedStep}
          />
        );
      case 3:
        return (
          <Location
            activeStep={activeStep}
            setActiveStep={setActiveStep}
            formData={formData}
            setFormData={setFormData}
            completedStep={completedStep}
            setCompletedStep={setCompletedStep}
          />
        );
      case 4:
        return (
          <AgencyAddress
            activeStep={activeStep}
            setActiveStep={setActiveStep}
            formData={formData}
            setFormData={setFormData}
            completedStep={completedStep}
            setCompletedStep={setCompletedStep}
          />
        );
      case 5:
        return (
          <AgencyName
            activeStep={activeStep}
            setActiveStep={setActiveStep}
            formData={formData}
            setFormData={setFormData}
            completedStep={completedStep}
            setCompletedStep={setCompletedStep}
          />
        );
      case 6:
        return (
          <AgencySize
            activeStep={activeStep}
            setActiveStep={setActiveStep}
            formData={formData}
            setFormData={setFormData}
            completedStep={completedStep}
            setCompletedStep={setCompletedStep}
          />
        );
      default:
        return "Unknown step";
    }
  };
  return (
    <Component>
      {/* <Routes> */}
      {/* {/* <Route
                    path='/number'
                    element={
                        <NumberComponent
                            activeStep={activeStep}
                            setActiveStep={setActiveStep}
                        />
                    }
                />
                <Route
                    path='/email'
                    element={
                        <EmailAndPass
                            activeStep={activeStep}
                            setActiveStep={setActiveStep}
                        />
                    }
                />
                <Route
                    path='/name'
                    element={
                        <Name
                            activeStep={activeStep}
                            setActiveStep={setActiveStep}
                        />
                    }
                />
                <Route
                    path='/location'
                    element={
                        <Location
                            activeStep={activeStep}
                            setActiveStep={setActiveStep}
                        />
                    }
                /> */}
      {/* </Routes> } */}
      {getStepContent(activeStep)}
    </Component>
  );
};

export default FormComponent;
