const CountryAnimation = keyframes`
    0%{
      transform: scale(0);
    }
    /* 0%, 20%, 50%, 80%, 100% {transform: translateY(0);}  */
    35% {transform: scale(1.05)} 
   /* 60% {transform: translateY(-5px);}  */
    100%{
      transform: scale(1);
    }
`;
/* 0:00:00:00 (scale 0 )
0:00:00:06 (scale 120)
0:00:00:14 (scale 96)
0:00:00:23 (scale 100)
0:00:01:04 (scale 99)
0:00:04:19 (sacle 99.4)
0:00:05:10 (scale 105.1)
0:00:05:17 (scale 81.8) */
const MobileAnimation = keyframes`
    0%{
      transform: scale(0) ;
      

    }
    2%{
      transform: scale(1.467) translateY(-25%);
    }
    5%{
      transform: scale(1.173) translateY(-25%);
    }
    7%{
      transform: scale(1.222) translateY(-25%);
    }
    20%{
      transform: scale(1.210) translateY(-25%);
      /* position: absolute; */
    }
    81%{
      transform: scale(1.280) translateY(-30%);
      /* position: absolute; */
    }
    98%{
      /* transform: scale(1.2); */
      transform: scale(1) ;
    }
    99%{
      /* transform: scale(1.2); */
      /* transform: scale(1) translateY(0%); */
    }
    100%{
      transform: scale(1);
    }
    
`;
