import React, { useEffect, useRef, useState } from "react";
import styled, { keyframes } from "styled-components";
import {
  TextField,
  Autocomplete,
  FormControl,
  IconButton,
  Input,
  InputAdornment,
  InputLabel,
  CircularProgress,
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import {
  ArrowRightAlt,
  Search,
  Visibility,
  VisibilityOff,
} from "@mui/icons-material";
import Swal from "sweetalert2";
import { flag } from "../flag";
import { Error } from "../GlobalStyle";
import { bucketUrl } from "../config";
const CountryAnimation = keyframes`
    0%{
      transform: scale(0);
    }
    /* 0%, 20%, 50%, 80%, 100% {transform: translateY(0);}  */
    40% {transform: scale(1.05)} 
   /* 60% {transform: translateY(-5px);}  */
    100%{
      transform: scale(1);
    }
`;
const FormDiv = styled.form`
  @media (min-height: 640px) {
    margin-top: 6vh;
  }
  /* width: 40%; */
  /* height: 80%; */
  display: flex;
  flex-direction: column;
  text-align: center;
  /* align-items: center; */
  /* justify-content: space-between; */
  font-family: "Roboto", sans-serif;
  max-width: 650px;
  /* position: relative; */
  @media (min-width: 4000px) {
    margin-top: 20vh;
    justify-self: center;
    padding: 0 5rem;
  }
  /* background-color: #000; */
`;
const Upper = styled.div``;
const Title = styled.h2`
  font-weight: 500;
  margin: 0.6rem;
  font-size: 1.9rem;
`;
const Message = styled.h5`
  font-weight: 400;
  font-size: 1.3rem;
  padding: 0;
  margin: 0;
`;
const InputComponent = styled.div`
  width: 100%;
  height: 50%;
  @media (min-height: 600px) {
    margin: 2.5rem 0;
    /* margin-top: 6vh; */
  }
  /* margin-bottom: 4rem; */
  background-color: #f9f9fa;
  display: flex;
  flex-direction: column;
  padding: 3rem 0;
  align-items: center;
  justify-content: center;
  position: relative;
  @media (min-width: 5000px) {
    /* justify-self: center; */
    padding: 1.2rem 1rem;
  }
`;
const Label = styled.div`
  font-weight: 400;
  margin: 10px 0;
`;
const Bold = styled.span`
  font-weight: 700;
`;
const InputLine = styled.div`
  margin: 30px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 80%;
  position: relative;
`;
const FlagComponent = styled.div`
  flex: 1;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
`;
const Flag = styled.img`
  width: 35px;
  height: auto;
`;
const FlagBig = styled.img`
  width: 2.5rem;
  height: auto;
`;

const DropDown = styled(KeyboardArrowDownIcon)`
  font-weight: 100;
`;
const CountryList = styled.div`
  padding: 2rem 1rem;
  position: absolute;
  max-height: 60vh;
  overflow-y: scroll;
  background-color: white;
  z-index: 100;
  top: -60%;
  left: 0;
  width: ${(props) => props.width}px;
  display: ${(props) => (props.isCountriesViewed ? "flex" : "none")};
  transform: ${(props) => (props.isCountriesViewed ? "1" : "0")};
  animation: ${CountryAnimation};
  animation-duration: 600ms;
  flex-direction: column;
  box-shadow: 0px 1px 16px rgba(0, 0, 0, 0.12), 0px 6px 12px rgba(0, 0, 0, 0.12),
    0px 4px 8px rgba(0, 0, 0, 0.12);
  /* transition:1s ease-in-out; */
`;
const CountryTable = styled.ul`
  padding: 0;
`;
const CountryLi = styled.li`
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
`;
const CountryLast = styled.div`
  display: flex;
  align-items: center;
`;
const CountryName = styled.h5`
  margin: 0;
  margin-left: 0.6rem;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 0.9rem;

  color: #19191d;
`;
const CountryCode = styled.h6`
  margin: 0;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  color: #787885;
  font-size: 0.9rem;
  mix-blend-mode: normal;
`;

const Number = styled.div`
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  flex: 3;
  margin-left: 20px;
`;
const Code = styled.input`
  margin-right: 5px;
  font-size: 1rem;
  width: 10%;
  height: 32px;
  min-width: 5ch;
  border: ${(props) =>
    props.currentCountry
      ? "0.001px solid rgba(0, 0, 0, 0.05)"
      : "0.001px solid red"};
  outline: none;
  background-color: transparent;
  color: black;
  font-weight: 600;
  padding: 0;
`;
const InputText = styled(TextField)`
  width: 80%;
`;
const InputWithIconPhone = styled(Input)`
  width: 100%;
  /* margin: 15px 0 !important;
  padding: 3px 2px; */
`;

const Button = styled.button`
  margin: 20px 0;
  padding: 8px;
  border-radius: 6px;
  outline: none;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  border: none;
  background-color: ${(props) => (props.isDisabled ? "#E1E1E3" : "#FFD740")};
  color: ${(props) => (props.isDisabled ? "#5A5B6A" : "#000000")};
  display: flex;
  align-items: center;
  justify-content: ${(props) => (props.loading ? "space-between" : "center")};
  &:hover {
    background-color: #ffc400;
    background-color: ${(props) => (props.isDisabled ? "#E1E1E3" : "#FFC400")};
  }
`;
const BlurDiv = styled.div`
  width: 100vw;
  height: 100vh;
  position: absolute;
  background-color: transparent;
  top: 0;
  left: 0;
  z-index: 99;
  display: ${(props) => (props.isCountriesViewed ? "block" : "none")};
`;
const NumberComponent = ({
  activeStep,
  setActiveStep,
  formData,
  setFormData,
  completedStep,
  setCompletedStep,
}) => {
  const [countries, setCountries] = useState([]);

  const [currentCountry, setCurrentCountry] = useState({});
  const [countryCode, setCountryCode] = useState(91);
  const [defaultNumber, setDefaultNumber] = useState("");
  const [filteredCountries, setFilteredCountries] = useState([]);
  const [width, setWidth] = useState(0);
  const [height, setHeight] = useState(0);
  const [error, setError] = useState(false);
  const [loading, setLoading] = useState(false);

  const navigate = useNavigate();
  const refWidth = useRef();
  const inputRef = useRef();
  // const ref=useRef();
  const [isViewd, setIsViewd] = useState(false);
  const [number, setNumber] = useState("");
  useEffect(() => {}, []);
  const changeCountry = (country) => {
    if (country) {
      setCurrentCountry(country);
      setCountryCode(country.calling_code);
      setIsViewd(false);
    }
  };
  useEffect(() => {
    setHeight(window.innerHeight);

    setWidth(refWidth.current.clientWidth);

    // setActiveStep(-1);
  }, []);
  const handleSubmit = async (e) => {
    
    e.preventDefault();
    // const number = e.target.number.value;
    let isExist = false;
    if (!currentCountry) {
      Swal.fire("Invalid Code", "Invalid country code", "error");
      return;
    }
    if (number.length !== 10 || number[0]==="0") {
      setError(1);
      return;
    }
    setLoading(true);

    fetch("https://test.paplilabs.com/login_api/validatePhNum/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ phone_number: "+" + countryCode + number }),
    })
      .then((res) => res.json())
      .then((res) => {
        setLoading(false);
        if (res.user_exist === "True") {
          setError(2);
          isExist = true;
        } else {
          // localStorage.setItem(
          //   "formDatas",
          //   JSON.stringify({
          //     number: number,
          //     countryCode: countryCode,
          //   })
          // );
          let formDatas = JSON.parse(localStorage.getItem("formDatas")) || {};
          formDatas.number = number;
          formDatas.countryCode = countryCode;
          localStorage.setItem("formDatas", JSON.stringify(formDatas));
          const datas = {
            numberWithoutCode: number,
            countryCode: countryCode,
            number: "+" + countryCode + number,
          };
          setFormData((formData) => ({
            ...formData,
            ...datas,
          }));
          setActiveStep(1);
          if (completedStep < 1) {
            setCompletedStep(1);
          }
        }
      });
    // navigate('/email')
  };
  useEffect(() => {
    const datas = JSON.parse(localStorage.getItem("formDatas")) || {};
    setCountryCode(formData.countryCode || 91);
    setNumber(formData.numberWithoutCode);
    const getCountries = async () => {
      fetch(
        "https://test.paplilabs.com/login_api/callingCode/?limit=249&offset=0"
      )
        .then((res) => res.json())
        .then((res) => {
          const countriesMap = [];
          res.results.map((country) => {
            const flagName = country.flag.split("/");
            if (country.calling_code == 93) {
              countriesMap.push({
                name: country.name,
                calling_code: country.calling_code,
                flag: flag + "/" + country.name,
              });
            } else {
              countriesMap.push({
                name: country.name,
                calling_code: country.calling_code,
                flag: flag + "/" + flagName[flagName.length - 1].split(".")[0],
              });
            }
          });
          setCountries(countriesMap);
          setFilteredCountries(countriesMap);
          let selectedCountry = [];
          if (formData.countryCode) {
            selectedCountry = countriesMap.filter(
              (country) => country.calling_code == formData.countryCode
            );
            setCurrentCountry(...selectedCountry);

            setCountryCode(selectedCountry[0].calling_code);
 
          } else {
            selectedCountry = countriesMap.filter(
              (country) => country.calling_code == 91
            );
            setCurrentCountry(...selectedCountry);
            
            setCountryCode(selectedCountry[0].calling_code);
          }
        });
    };
    getCountries();
  }, []);

  const handleChange = (e) => {
    setCountryCode(e.target.value.split("+")[1] || "");
    const selectedCountry = countries.filter(
      (country) => country.calling_code == e.target.value.split("+")[1]
    );

    setCurrentCountry(...selectedCountry);
  };
  const handleSearch = (e) => {
    if (e.target.value === "") {
      setFilteredCountries(countries);
    } else {
      setFilteredCountries(
        countries.filter(
          (country) =>
            country.name.toLowerCase().includes(e.target.value.toLowerCase()) ||
            (country.calling_code.toString().includes(e.target.value) &&
              country)
        )
      );
    }
  };
  const handleNumberChange = (e) => {
    setNumber(e.target.value);
    if (e.target.value.length === 10) {
      setError(false);
    }
  };
  return (
    <FormDiv onSubmit={(e) => handleSubmit(e)}>
      <BlurDiv onClick={() => setIsViewd(false)} isCountriesViewed={isViewd} />
      <Upper>
        <Title>Welcome!</Title>
        <Message>Let’s get you started with a free Account.</Message>
      </Upper>
      <InputComponent ref={refWidth}>
        <Label>
          Lets get started by entering your <Bold>phone number</Bold>
        </Label>
        <CountryList isCountriesViewed={isViewd} width={width - 150}>
          <FormControl sx={{ width: "100%" }} variant="outlined">
            <InputLabel htmlFor="outlined-adornment-password">
              Search
            </InputLabel>

            <Input
              id="outlined-adornment-password"
              onChange={handleSearch}
              ref={inputRef}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    edge="end"
                  >
                    <Search />
                  </IconButton>
                </InputAdornment>
              }
              label="Search"
            />
          </FormControl>
          <CountryTable>
            {filteredCountries.map((country) => (
              <CountryLi onClick={() => changeCountry(country)}>
                <CountryLast>
                  <FlagBig loading="lazy" src={country.flag} />
                  <CountryName>{country.name}</CountryName>
                </CountryLast>
                <CountryCode>{country.calling_code}</CountryCode>
              </CountryLi>
            ))}
          </CountryTable>
        </CountryList>
        <InputLine>
          <FlagComponent onClick={() => setIsViewd(!isViewd)}>
            <Flag
              src={(currentCountry && currentCountry.flag) || "/india.png"}
            />
            <DropDown />
          </FlagComponent>
          <Number>
            <Code
              // defaultValue="+91"
              currentCountry={currentCountry}
              value={"+" + countryCode}
              onChange={(e) => handleChange(e)}
            />
            <FormControl
              // sx={{ width: isNumber ? "80%" : "100%" }}
              variant="standard"
            >
              <InputWithIconPhone
                placeholder="9365722xxx"
                name="number"
                error={error}
                type="number"
                value={number}
                onChange={(e) => handleNumberChange(e)}
                // onBlur
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      onClick={() => setNumber("")}
                      aria-label="toggle password visibility"
                    >
                      {error ? <img src={bucketUrl + "/cross.svg"} /> : <></>}
                    </IconButton>
                  </InputAdornment>
                }
              />
            </FormControl>
            {error || !currentCountry ? (
              <Error severity="error" icon={<></>}>
                {error === 1
                  ? "Enter a valid 10 digit number"
                  : error === 2
                  ? "Phone Number already exist"
                  : "Invalid country code"}
                {/* {!currentCountry ? "Invalid country code" : ""} */}
              </Error>
            ) : (
              <></>
            )}
          </Number>
        </InputLine>
      </InputComponent>
      <Button disabled={error} isDisabled={error} loading={loading}>
        {loading ? <div /> : <></>}
        <span>Continue</span>
        {loading ? <CircularProgress size="1rem" /> : <></>}
      </Button>
    </FormDiv>
  );
};

export default NumberComponent;
